import { graphql } from "gatsby";
import React from "react";
import { SEO } from "../components/misc/seo";
import { Layout } from "../components/structure/layout";
import { BlogArticle } from "../components/widgets/blog-article";
const BlogArticleTemplate = ({ data: { markdownRemark, site }, }) => {
    const { fields, frontmatter, html } = markdownRemark;
    return (<Layout>
      <SEO title={frontmatter.title}/>
      <BlogArticle author={frontmatter.author} authorImageUrl={frontmatter.authorImageUrl} authorUrl={frontmatter.authorUrl} baseUrl={site.siteMetadata.baseUrl} date={frontmatter.date} featuredImage={frontmatter.featuredImage?.childImageSharp?.fluid} htmlContent={html} path={frontmatter.path} readingTime={fields.readingTime.text} tags={frontmatter.tags} title={frontmatter.title} twitterAuthor={site.siteMetadata.author}/>
    </Layout>);
};
export default BlogArticleTemplate;
export const pageQuery = graphql `
  query getBlogArticle($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      fields {
        readingTime {
          text
        }
      }
      frontmatter {
        author
        authorImageUrl
        authorUrl
        date(formatString: "MMMM DD, YYYY")
        featuredImage {
          childImageSharp {
            fluid(maxWidth: 800) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        path
        tags
        title
      }
      html
    }
    site {
      siteMetadata {
        author
        baseUrl
      }
    }
  }
`;
